  --color-enf-dark-1: #222b77;
  --color-enf-dark-2: #606dd2;
  --color-enf-light-1: #f1f2fd;
  --color-enf-light-2: #c0c5ed;
}
.header {
  position: fixed;
  top: 0;
  height: 50px;
  width: 100%;
  background-color: var(--color-enf-light-2);
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 2rem;
}
.main {
  position: relative;
  top: 50px;
  height: calc(100vh - 50px);
  width: 100%;
  background-color: var(--color-enf-light-1);
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  padding-top: 50px;
}
.nav-seccion {
  position: absolute;
  width: 200px;
  height: calc(100% - 50px);
  top: 50px;
  left: -170px;
  padding: 0 2rem 0 1rem;
  background-color: var(--color-enf-dark-2);
  -webkit-box-shadow: 10px 0 10px 0 rgba(0, 0, 0, 0);
          box-shadow: 10px 0 10px 0 rgba(0, 0, 0, 0);
  -webkit-transition: left 0.5s, background-color 0.5s, -webkit-box-shadow 0.5s;
  transition: left 0.5s, background-color 0.5s, -webkit-box-shadow 0.5s;
  transition: left 0.5s, background-color 0.5s, box-shadow 0.5s;
  transition: left 0.5s, background-color 0.5s, box-shadow 0.5s, -webkit-box-shadow 0.5s;
}
.nav-seccion:hover {
  -webkit-box-shadow: 10px 0 20px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 10px 0 20px 0 rgba(0, 0, 0, 0.1);
  background-color: var(--color-enf-light-1);
  left: 0px;
}
.list-group {
  width: 100%;
