  font-size: 1.125rem;
  font-weight: 600;
  text-decoration: none;
  line-height: 2rem;
  color: var(--color-enf-dark-1);
  border-left: var(--color-enf-dark-1) 0 solid;
  -webkit-transition: border-left 0.2s;
  transition: border-left 0.2s;
}
.list-group a:hover {
  background-color: #f1f2fd;
  border-left: var(--color-enf-dark-1) 0.5rem solid;
}
.list-title {
  color: var(--color-enf-dark-2);
